import * as wasm from './webassembly_bg.wasm';

/**
* @param {number} num
* @returns {number}
*/
export function factorialize(num) {
    var ret = wasm.factorialize(num);
    return ret >>> 0;
}

/**
* @param {number} n
* @returns {number}
*/
export function factorialize_fib(n) {
    var ret = wasm.factorialize_fib(n);
    return ret >>> 0;
}

